.project-card-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
    flex-wrap: wrap;
    padding: 25px;
}

.project-card {
    width: 400px;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.1);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    margin: 20px;
    background-color: rgb(250, 249, 249);
}

.project-card:hover {
    box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.4);
    transition: all 0.3s;
}

.image-container {
    width: 100%;
    border-radius: 20px 20px 0 0;
    height: 230px;

}

.project-link {
    border-radius: inherit;
}

.project-image {
    display: block;
    border-radius: inherit;
    max-height: 100%;
    width: 100%;
    height: 230px;
}

.project-details-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-self: flex-start;
    padding: 14px 20px;
    border-radius: 0 0 20px 20px;
}

.project-name {
    margin: 0px;
    font-size: 22px;
}

.project-desc {
    font-weight: 300;
    font-size: 15px;
    line-height: 24px;
    color: #666469;
    padding: 0;

}



@media only screen and (max-width: 640px) {
    .project-cards-container {
        padding: 20px;
    }

    .project-card {
        width: 280px;

    }

    .project-desc {
        font-weight: 300;
        font-size: 14px;
        line-height: 20px;
        color: #666469;

    }
}